import React from 'react';
import { 
  BarChart, Bar, XAxis, YAxis, CartesianGrid, Tooltip, Legend,
  PieChart, Pie, Cell,
  LineChart, Line,
  ResponsiveContainer 
} from 'recharts';
import { 
  FiDownload, 
  FiRefreshCw, 
  FiVideo, 
  FiPhone, 
  FiMail, 
  FiMessageSquare 
} from 'react-icons/fi';

const AnalyticsDashboard = () => {
  const communicationData = [
    { method: 'LinkedIn Post', count: 45, success: 38 },
    { method: 'LinkedIn Message', count: 65, success: 52 },
    { method: 'Email', count: 85, success: 63 },
    { method: 'Phone Call', count: 32, success: 28 },
    { method: 'Other', count: 15, success: 12 }
  ];

  const trendData = Array.from({ length: 12 }, (_, i) => ({
    month: new Date(2024, i).toLocaleString('default', { month: 'short' }),
    overdue: Math.floor(Math.random() * 10),
    completed: Math.floor(Math.random() * 50 + 20)
  }));

  const COLORS = ['#4F46E5', '#10B981', '#3B82F6', '#F59E0B', '#EF4444'];

  return (
    <div className="space-y-6">
      <div className="flex justify-between items-center">
        <h1 className="text-2xl font-bold text-gray-900">Analytics Dashboard</h1>
        <div className="flex space-x-4">
          <button className="btn-secondary flex items-center">
            <FiRefreshCw className="mr-2" />
            Refresh
          </button>
          <button className="btn-primary flex items-center">
            <FiDownload className="mr-2" />
            Export Report
          </button>
        </div>
      </div>

      <div className="grid grid-cols-1 lg:grid-cols-2 gap-6">
        {/* Communication Frequency */}
        <div className="bg-white rounded-xl shadow-sm p-6">
          <h2 className="text-lg font-semibold mb-4">Communication Frequency</h2>
          <ResponsiveContainer width="100%" height={300}>
            <BarChart data={communicationData}>
              <CartesianGrid strokeDasharray="3 3" />
              <XAxis dataKey="method" />
              <YAxis />
              <Tooltip />
              <Legend />
              <Bar dataKey="count" fill="#4F46E5" name="Total" />
              <Bar dataKey="success" fill="#10B981" name="Successful" />
            </BarChart>
          </ResponsiveContainer>
        </div>

        {/* Effectiveness Distribution */}
        <div className="bg-white rounded-xl shadow-sm p-6">
          <h2 className="text-lg font-semibold mb-4">Communication Effectiveness</h2>
          <ResponsiveContainer width="100%" height={300}>
            <PieChart>
              <Pie
                data={communicationData}
                dataKey="success"
                nameKey="method"
                cx="50%"
                cy="50%"
                outerRadius={100}
                label
              >
                {communicationData.map((entry, index) => (
                  <Cell key={`cell-${index}`} fill={COLORS[index % COLORS.length]} />
                ))}
              </Pie>
              <Tooltip />
              <Legend />
            </PieChart>
          </ResponsiveContainer>
        </div>

        {/* Trends Over Time */}
        <div className="bg-white rounded-xl shadow-sm p-6 lg:col-span-2">
          <h2 className="text-lg font-semibold mb-4">Communication Trends</h2>
          <ResponsiveContainer width="100%" height={300}>
            <LineChart data={trendData}>
              <CartesianGrid strokeDasharray="3 3" />
              <XAxis dataKey="month" />
              <YAxis />
              <Tooltip />
              <Legend />
              <Line 
                type="monotone" 
                dataKey="completed" 
                stroke="#10B981" 
                name="Completed"
              />
              <Line 
                type="monotone" 
                dataKey="overdue" 
                stroke="#EF4444" 
                name="Overdue"
              />
            </LineChart>
          </ResponsiveContainer>
        </div>
      </div>

      {/* Real-time Activity Log */}
      <div className="bg-white rounded-xl shadow-sm p-6">
        <h2 className="text-lg font-semibold mb-4">Activity Log</h2>
        <div className="space-y-4">
          {/* Activity items */}
          <ActivityLogItem 
            type="video"
            company="Tech Solutions Inc."
            action="Completed video call"
            time="5 minutes ago"
          />
          <ActivityLogItem 
            type="email"
            company="Global Innovations"
            action="Sent follow-up email"
            time="1 hour ago"
          />
          <ActivityLogItem 
            type="phone"
            company="EcoSmart Solutions"
            action="Scheduled phone call"
            time="2 hours ago"
          />
        </div>
      </div>
    </div>
  );
};

const ActivityLogItem = ({ type, company, action, time }) => {
  const getIcon = () => {
    switch (type) {
      case 'video':
        return <FiVideo />;
      case 'phone':
        return <FiPhone />;
      case 'email':
        return <FiMail />;
      default:
        return <FiMessageSquare />;
    }
  };

  return (
    <div className="flex items-center space-x-4 p-3 hover:bg-gray-50 rounded-lg">
      <div className="w-8 h-8 rounded-full bg-purple-100 text-purple-600 flex items-center justify-center">
        {getIcon()}
      </div>
      <div className="flex-1">
        <p className="text-sm font-medium text-gray-900">{company}</p>
        <p className="text-sm text-gray-500">{action}</p>
      </div>
      <span className="text-sm text-gray-400">{time}</span>
    </div>
  );
};

export default AnalyticsDashboard; 