// App.js
import React, { useState, useEffect } from 'react';
import { HashRouter as Router, Routes, Route, Navigate } from 'react-router-dom';
import DashboardLayout from './components/layout/DashboardLayout';

// Auth Pages
import Login from './pages/auth/Login';
import SignUp from './pages/auth/SignUp';
import LandingPage from './pages/LandingPage';

// Admin Pages
import CompanyManagement from './pages/admin/CompanyManagement';
import CommunicationMethodManagement from './pages/admin/CommunicationMethodManagement';

// User Pages
import Dashboard from './pages/user/Dashboard';
import CommunicationCalendar from './pages/calendar/CommunicationCalendar';
import AnalyticsDashboard from './pages/analytics/AnalyticsDashboard';

function App() {
  const [isLoading, setIsLoading] = useState(true);
  const [isAuthenticated, setIsAuthenticated] = useState(false);
  const [isAdmin, setIsAdmin] = useState(false);

  useEffect(() => {
    // Check if user is logged in
    const authStatus = localStorage.getItem('auth-status');
    const userRole = localStorage.getItem('user-role');
    
    if (authStatus) {
      setIsAuthenticated(true);
      setIsAdmin(userRole === 'admin');
    }
    
    setIsLoading(false);
  }, []);

  const handleLogin = (role) => {
    setIsAuthenticated(true);
    setIsAdmin(role === 'admin');
    localStorage.setItem('auth-status', 'true');
    localStorage.setItem('user-role', role);
  };

  const handleLogout = () => {
    setIsAuthenticated(false);
    setIsAdmin(false);
    localStorage.clear();
  };

  if (isLoading) {
    return (
      <div className="flex items-center justify-center min-h-screen">
        <div className="animate-spin rounded-full h-8 w-8 border-b-2 border-violet-600"></div>
      </div>
    );
  }

  return (
    <Router>
      {isAuthenticated ? (
        <DashboardLayout onLogout={handleLogout}>
          <Routes>
            {/* Admin Routes */}
            {isAdmin && (
              <>
                <Route path="/admin/companies" element={<CompanyManagement />} />
                <Route 
                  path="/admin/communication-methods" 
                  element={<CommunicationMethodManagement />} 
                />
              </>
            )}

            {/* User Routes */}
            <Route path="/dashboard" element={<Dashboard />} />
            <Route path="/calendar" element={<CommunicationCalendar />} />
            <Route path="/analytics" element={<AnalyticsDashboard />} />

            {/* Redirect to dashboard if accessing root while authenticated */}
            <Route path="/" element={<Navigate to="/dashboard" replace />} />
            
            {/* Catch unauthorized access attempts */}
            {!isAdmin && (
              <Route path="/admin/*" element={<Navigate to="/dashboard" replace />} />
            )}

            {/* Catch all other routes */}
            <Route path="*" element={<Navigate to="/dashboard" replace />} />
          </Routes>
        </DashboardLayout>
      ) : (
        <Routes>
          {/* Public Routes */}
          <Route path="/" element={<LandingPage />} />
          <Route 
            path="/login" 
            element={<Login onLogin={handleLogin} />} 
          />
          <Route path="/signup" element={<SignUp />} />

          {/* Redirect to login for protected routes */}
          <Route path="*" element={<Navigate to="/login" replace />} />
        </Routes>
      )}
    </Router>
  );
}

export default App;
