import React, { useState } from 'react';
import { FiVideo, FiPhone, FiMail, FiMessageSquare, FiChevronLeft, FiChevronRight } from 'react-icons/fi';

const CommunicationCalendar = () => {
  const [currentDate, setCurrentDate] = useState(new Date());
  const [selectedDate, setSelectedDate] = useState(null);
  const [events] = useState([
    {
      id: 1,
      title: 'Video Call - Tech Solutions Inc.',
      start: new Date(2024, 1, 15, 10, 0),
      end: new Date(2024, 1, 15, 11, 0),
      type: 'video',
      company: 'Tech Solutions Inc.',
      description: 'Quarterly Review Meeting'
    },
    // Add more events as needed
  ]);

  const getDaysInMonth = (date) => {
    const year = date.getFullYear();
    const month = date.getMonth();
    const days = new Date(year, month + 1, 0).getDate();
    const firstDay = new Date(year, month, 1).getDay();
    return { days, firstDay };
  };

  const { days, firstDay } = getDaysInMonth(currentDate);

  const prevMonth = () => {
    setCurrentDate(new Date(currentDate.getFullYear(), currentDate.getMonth() - 1));
  };

  const nextMonth = () => {
    setCurrentDate(new Date(currentDate.getFullYear(), currentDate.getMonth() + 1));
  };

  const getEventForDate = (date) => {
    return events.filter(event => 
      event.start.getDate() === date && 
      event.start.getMonth() === currentDate.getMonth() &&
      event.start.getFullYear() === currentDate.getFullYear()
    );
  };

  const weekDays = ['Sun', 'Mon', 'Tue', 'Wed', 'Thu', 'Fri', 'Sat'];
  const monthName = currentDate.toLocaleString('default', { month: 'long', year: 'numeric' });

  return (
    <div className="space-y-6">
      <div className="flex justify-between items-center">
        <h1 className="text-2xl font-bold text-gray-900">Communication Calendar</h1>
        <div className="flex space-x-4">
          <button className="btn-primary">Schedule Communication</button>
          <select className="form-select">
            <option>All Companies</option>
            <option>Tech Solutions Inc.</option>
            <option>Global Innovations</option>
          </select>
        </div>
      </div>

      <div className="bg-white rounded-xl shadow-sm p-6">
        {/* Calendar Header */}
        <div className="flex justify-between items-center mb-6">
          <button onClick={prevMonth} className="p-2 hover:bg-gray-100 rounded-full">
            <FiChevronLeft className="w-5 h-5" />
          </button>
          <h2 className="text-xl font-semibold">{monthName}</h2>
          <button onClick={nextMonth} className="p-2 hover:bg-gray-100 rounded-full">
            <FiChevronRight className="w-5 h-5" />
          </button>
        </div>

        {/* Calendar Grid */}
        <div className="grid grid-cols-7 gap-1">
          {/* Week days */}
          {weekDays.map(day => (
            <div key={day} className="text-center py-2 text-sm font-semibold text-gray-600">
              {day}
            </div>
          ))}

          {/* Empty cells for first week */}
          {Array.from({ length: firstDay }).map((_, index) => (
            <div key={`empty-${index}`} className="h-24 border border-gray-100" />
          ))}

          {/* Calendar days */}
          {Array.from({ length: days }).map((_, index) => {
            const date = index + 1;
            const dayEvents = getEventForDate(date);
            const isToday = new Date().getDate() === date && 
                           new Date().getMonth() === currentDate.getMonth() &&
                           new Date().getFullYear() === currentDate.getFullYear();

            return (
              <div 
                key={date}
                className={`h-24 border border-gray-100 p-1 ${
                  isToday ? 'bg-blue-50' : ''
                } ${
                  selectedDate === date ? 'ring-2 ring-blue-500' : ''
                }`}
                onClick={() => setSelectedDate(date)}
              >
                <div className="text-right">
                  <span className={`text-sm ${
                    isToday ? 'bg-blue-500 text-white rounded-full px-2 py-1' : ''
                  }`}>
                    {date}
                  </span>
                </div>
                <div className="mt-1">
                  {dayEvents.map(event => (
                    <div 
                      key={event.id}
                      className="text-xs p-1 mb-1 rounded truncate"
                      style={{
                        backgroundColor: 
                          event.type === 'video' ? '#7C3AED' :
                          event.type === 'phone' ? '#10B981' :
                          event.type === 'email' ? '#3B82F6' : '#6B7280',
                        color: 'white'
                      }}
                    >
                      {event.title}
                    </div>
                  ))}
                </div>
              </div>
            );
          })}
        </div>
      </div>

      {/* Event Details */}
      {selectedDate && (
        <div className="bg-white rounded-xl shadow-sm p-6 mt-6">
          <h3 className="text-lg font-semibold mb-4">
            Events for {selectedDate} {monthName}
          </h3>
          <div className="space-y-4">
            {getEventForDate(selectedDate).map(event => (
              <div key={event.id} className="flex items-start space-x-4 p-4 bg-gray-50 rounded-lg">
                <div className={`p-2 rounded-lg ${
                  event.type === 'video' ? 'bg-purple-100 text-purple-600' :
                  event.type === 'phone' ? 'bg-green-100 text-green-600' :
                  event.type === 'email' ? 'bg-blue-100 text-blue-600' :
                  'bg-gray-100 text-gray-600'
                }`}>
                  {event.type === 'video' && <FiVideo className="w-5 h-5" />}
                  {event.type === 'phone' && <FiPhone className="w-5 h-5" />}
                  {event.type === 'email' && <FiMail className="w-5 h-5" />}
                  {event.type === 'linkedin' && <FiMessageSquare className="w-5 h-5" />}
                </div>
                <div>
                  <h4 className="font-medium">{event.title}</h4>
                  <p className="text-sm text-gray-600">{event.description}</p>
                  <p className="text-sm text-gray-500 mt-1">
                    {event.start.toLocaleTimeString()} - {event.end.toLocaleTimeString()}
                  </p>
                </div>
              </div>
            ))}
          </div>
        </div>
      )}
    </div>
  );
};

export default CommunicationCalendar; 