import React, { useState } from 'react';
import { FiMove, FiEdit2, FiTrash2, FiPlus } from 'react-icons/fi';
import { DragDropContext, Droppable, Draggable } from 'react-beautiful-dnd';

const CommunicationMethodManagement = () => {
  const [methods, setMethods] = useState([
    {
      id: '1',
      name: 'LinkedIn Post',
      description: 'Share updates on company LinkedIn page',
      sequence: 1,
      mandatory: true
    },
    {
      id: '2',
      name: 'LinkedIn Message',
      description: 'Direct message to company representatives',
      sequence: 2,
      mandatory: true
    },
    {
      id: '3',
      name: 'Email',
      description: 'Email communication',
      sequence: 3,
      mandatory: true
    },
    {
      id: '4',
      name: 'Phone Call',
      description: 'Direct phone communication',
      sequence: 4,
      mandatory: false
    },
    {
      id: '5',
      name: 'Other',
      description: 'Other forms of communication',
      sequence: 5,
      mandatory: false
    }
  ]);

  const onDragEnd = (result) => {
    if (!result.destination) return;

    const items = Array.from(methods);
    const [reorderedItem] = items.splice(result.source.index, 1);
    items.splice(result.destination.index, 0, reorderedItem);

    // Update sequence numbers
    const updatedItems = items.map((item, index) => ({
      ...item,
      sequence: index + 1
    }));

    setMethods(updatedItems);
  };

  return (
    <div className="space-y-6">
      <div className="flex justify-between items-center">
        <h1 className="text-2xl font-bold text-gray-900">Communication Methods</h1>
        <button className="btn-primary flex items-center">
          <FiPlus className="mr-2" />
          Add Method
        </button>
      </div>

      <DragDropContext onDragEnd={onDragEnd}>
        <Droppable droppableId="methods">
          {(provided) => (
            <div
              {...provided.droppableProps}
              ref={provided.innerRef}
              className="space-y-4"
            >
              {methods.map((method, index) => (
                <Draggable
                  key={method.id}
                  draggableId={method.id}
                  index={index}
                >
                  {(provided) => (
                    <div
                      ref={provided.innerRef}
                      {...provided.draggableProps}
                      className="bg-white rounded-lg shadow-sm p-4 flex items-center justify-between"
                    >
                      <div className="flex items-center space-x-4">
                        <div
                          {...provided.dragHandleProps}
                          className="text-gray-400 cursor-move"
                        >
                          <FiMove className="w-5 h-5" />
                        </div>
                        <div>
                          <h3 className="text-lg font-medium text-gray-900">
                            {method.name}
                          </h3>
                          <p className="text-sm text-gray-500">
                            {method.description}
                          </p>
                        </div>
                      </div>
                      <div className="flex items-center space-x-4">
                        <span className={`px-3 py-1 rounded-full text-xs font-medium ${
                          method.mandatory 
                            ? 'bg-purple-100 text-purple-800'
                            : 'bg-gray-100 text-gray-800'
                        }`}>
                          {method.mandatory ? 'Mandatory' : 'Optional'}
                        </span>
                        <button className="text-gray-400 hover:text-gray-600">
                          <FiEdit2 className="w-5 h-5" />
                        </button>
                        <button className="text-gray-400 hover:text-red-600">
                          <FiTrash2 className="w-5 h-5" />
                        </button>
                      </div>
                    </div>
                  )}
                </Draggable>
              ))}
              {provided.placeholder}
            </div>
          )}
        </Droppable>
      </DragDropContext>
    </div>
  );
};

export default CommunicationMethodManagement; 